import React from 'react';

const AXIOS = require('axios');

class RestClient {
  constructor() {
    this.origin = ''
  }
  domain(config) {
    this.origin = config.endpoint;
  }
  getClient() {
    var axiosInstance = AXIOS.create({
      baseURL: this.origin,
    });
    return axiosInstance
  }
}

export default RestClient;